.work {
    padding: 50px 0 20px;
    /* background-color: dodgerblue; */

    .work-headline h3 {
        font-size: 48px;
        font-weight: 800;
        line-height: 1.5;
        letter-spacing: -2px;
    }

    .work-projects {
        margin-top: 30px;
        display: flex;
        flex-wrap: wrap;
    }
}

@media (max-width: 1100px) {
	.work .work-headline h3 {
        font-size: 38px;
    }
}
  
@media (max-width: 768px) {
	.work .work-headline h3 {
        font-size: 32px;
    }
}

@media (max-width: 370px) {
	.work .work-headline h3 {
        text-align: center;
        font-size: 24px;
    }
}